@charset 'utf-8';

/* ========================================================================== */
/* IMPORT VARIABLES */
/* ========================================================================== */

@import "variables/_colors";
@import "variables/_fonts";

/* ========================================================================== */
/* IMPORT MIXINS */
/* ========================================================================== */

@import "mixins/_position";

/* ========================================================================== */
/* MAIN STYLES */
/* ========================================================================== */

/* -------------------------------------------------------------------------- */
/* REM SETUP */
/* -------------------------------------------------------------------------- */

html {
    font-size: 10px;
}
@media screen and (max-width: 1400px) {
    html {
        font-size: 9px;
    }
}
@media screen and (max-width: 1200px) {
    html {
        font-size: 8.5px;
    }
}

body {
    font-size: 1.5rem;
    color: $gray-800;
    background: $gray-100;
    font-family: $font-family-system;
    overflow-x: hidden;
    position: relative;
    width:100%;
    margin: 0 auto;
    font-weight: 400;
    line-height: 1.5;
    text-align: left;
}

@media (min-width: 576px){
    .container {
        max-width: 100%;
    }
}
@media (min-width: 768px){
    .container {
        max-width: 100%;
    }
}
@media (min-width: 992px){
    .container {
        max-width: 960px;
    }
}
@media (min-width: 1200px){
    .container {
        max-width: 1220px;
    }
}

/* -------------------------------------------------------------------------- */
/* GLOBAL BOX SIZING */
/* -------------------------------------------------------------------------- */

*, *:after, *:before {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: auto;
    text-rendering: optimizeLegibility;
}
/* -------------------------------------------------------------------------- */
/* CLEAR */
/* -------------------------------------------------------------------------- */

.clear {
    *zoom: 1;

    &::before, &::after {
        content: ' ';
        display: table; 
    }
    &::after {
        clear: both;
    }
}

/* -------------------------------------------------------------------------- */
/* IMAGES RESPOSIV */
/* -------------------------------------------------------------------------- */

img {
    max-width: 100%;
    vertical-align: middle;
    height: auto;
    transition: 0.3s all ease;
}

/* -------------------------------------------------------------------------- */
/* OBJECT FIT COVER IMAGE */
/* -------------------------------------------------------------------------- */

.bgCover__wrap{
    position: relative;
    z-index: 1;
}
.bgCover__img{
    object-fit: cover;
    @include absolute-position(0px, 0px, 0px, 0px);
    height: 100%;
    width: 100%;
    z-index: -1;
}

/* -------------------------------------------------------------------------- */
/* SELECTIONS */
/* -------------------------------------------------------------------------- */

::selection{
    color: $white;
    background: $brand-primary;
}
::placeholder {
    color: $text-primary;
    opacity: 0.75;
    font-size: 1.6rem;
}

/* ========================================================================== */
/* PARTIALS */
/* ========================================================================== */

